import { Injectable } from '@angular/core';

@Injectable()
export class PostsService {
public posts=[];
  constructor() {
fetch("http://jsonplaceholder.typicode.com/posts")
  .then((res)=>res.json())
  .then((data)=>this.posts.push(data))
  .catch((err)=>console.log(err))
  console.log(this.posts);
   }

}



